
.popup-background{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
    .popup-content{
        position: relative;
        width: 100vw;
        height: 75vh;
        background-color: #fff;
        @media (max-width:750px){
            height: 95vh;
        }
        .close-button{
            position: absolute;
            right: 30px;
            top: -40px;
            @media (max-width:750px){
                top: 0px;
                z-index: 9999;
            }
            img{
                width: 35px;
                height: 35px;
            }
        }
    }
}
.popUp__item{
    display: flex;
    justify-content: space-between;
    @media (max-width:750px){
        flex-direction: column;
    }
}
.popUp__text{
    display: flex;
    flex-direction: column;
    color: #000;
    z-index: 501;
    margin-left: 50px;
    .popUp__text__title{
        flex: 1;
        h3{
            font-family: "Gerhaus Regular";
            font-size: 4vw;
            line-height: 85%;
            @media (max-width:750px){
                font-size: 30px;
            }
        }
        p{
            font-weight: 600;
            margin: 30px 0 15px;
            font-family: "HeroLight";
            font-size: 29px;
            @media (max-width:750px){
                font-size: 20px;
                margin: 0px 0 0px;
            }
        }
    }
    .popUp__text__accorgion{
        margin-top: 15px;
        font-size: 25px;
        font-family: "HeroLight";
        flex: 3;
        button{
            color: #000;
            position: relative;
            display: inline-block;
            font-weight: 600;
            transition: .2s;
            @media (max-width:750px){
                display: none;
            }
            &:hover{
                transform: translateX(5px);
                color: #535353;
            }
            &::after{
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                height: 1px;
                width: 100%;
                background-color: #000;
            }
            img{
                position: absolute;
                right: -25%;
                top: 30%;
                color: #000;
                rotate: -180deg;
                width: 25px;
            }
        }
        .popup {
            margin-top: 10px;
            position: relative;
            background-color: #fff;
            max-width: 90%;
            max-height: 30vh;
            overflow: auto; /* Включаем прокрутку */
            -ms-overflow-style: none;
            scrollbar-width: none;
            font-size: 22px;
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
          }
        }
          .close {
            position: absolute;
            top: 0px;
            right: 10px;
            cursor: pointer;
          }
          .scrollable-content {
            max-height: 100%; /* Ограничиваем высоту контента */
            overflow-y: auto; /* Включаем прокрутку */
          }
    }
    .popUp__text__subtitle{
        display: flex;
        align-items: center;
        gap: 30px;
        h4{
            font-family: "Gerhaus Regular";
            font-size: 20px;
        }
        h5{
            font-family: "HeroLight";
            font-size: 20px;
        }
    }
}
.popUp__images{
    width: 60%;
    @media (max-width:750px){
        width: 95%;
        margin: 20px 2px 0;
     }
}
.swiperPopUp__item{
    height: 75vh;
    position: relative;
    padding-left: 27px;
    
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.nav__button{
    button{
        position: absolute;
        left: 0%;
        bottom: 10%;
        background-color: #212121;
        padding: 15px;
        border-radius: 50%;
        z-index: 9999999;
        &:hover img{
            scale: 1.1;
        }
        img{
            width: 30px;
            height: 30px;
            rotate: 90deg;
            transition: .3s ease-in-out;        
            
        }
    }
}