

.loader__wrapper{
	position: relative;
    display: flex;
	width: 100vw;
	height: 100vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: "all 0.25s ease-in-out";
    background-color: #191919;
    color: #dbd8d6;
	.loader__wrapper__title{
		overflow: hidden;
		padding: 0 5px;
		#svgPreloderTitle{
			margin-left: 10px;
			width: 550px;
			height: 100%;
			stroke: #fff;
			fill-opacity: 0;
			stroke-width: 1px;
			stroke-dasharray: 4500;
			animation: opasytiPrelouder 40s ease;
			animation-delay: 2s;
			@keyframes opasytiPrelouder {
				0%{stroke-dashoffset: 4500;}
				100%{stroke-dashoffset: 0;}
			}
			@media (max-width:700px){
				width: 420px;
			  }
			  @media (max-width:450px){
				width: 320px;
			  }
		}
	}
    
	.loader__wrapper__loading{
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		width: 100vw;
		height: 100vh;
		background: linear-gradient(to right, #2c5364, #203a43, #0f2027);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		
		#svgPreloder{
			padding: 5px;
			width: 130px;
			height: 130px;
			stroke: #fff;
			fill-opacity: 0;
			stroke-width: 1px;
			stroke-dasharray: 4500;
			animation: opasytiPrelouder 40s ease;
			@keyframes opasytiPrelouder {
				0%{stroke-dashoffset: 4500;}
				100%{stroke-dashoffset: 0;}
			}
		}
		.loader__wrapper__loading__name{
			overflow: hidden;
			margin-top: -15px;
			h2{
				font-family: "BeatriceDeck-medium";
				font-weight: 300;
				font-size: 25px;
				letter-spacing: 3px;
				animation: preloderName  1s ease-in-out;
				@keyframes preloderName {
					0%{opacity: 0; transform: translateY(50px);}
					90%{opacity: 1; transform: translateY(-2px);}
					100%{opacity: 1; transform: translateY(0px);}
				}
			}
		}
		
	}
}