

.picture__wrapper{
    background-color: #EBECF0;
}

.picture__contentent__flags{
   position: absolute;
   right: 8%;
   top: 0;
   @media (max-width:1100px){
      top: -10px;
   }
   .img__flags{
      display: block;
      width: 50px;
      img{
         width: 50px;

      }
   }
   .img__social{
      display: block;
      width: 40px;
      margin: 10px auto 0;
   }
}
.picture__container__hover:hover .picture__contentent{
   height: 100%;
   opacity: 1;
   @media (max-width:950px){
      display: none;
  }
 }

.picture__container__cards_1{
    display: grid; 

    grid-template-rows: 0.1fr 0.8fr 0.1fr;
    grid-template-columns: 1fr 2fr 2fr;
    
    gap: 20px;
    height: 100%;

    @media (max-width:650px){
      display: flex;
      flex-direction: column;
      padding: 20px;
   }
    video{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .picture__contentent__bob{
      flex-direction: column;
      padding-top: 110px;
    }
    
    #item-0 {
        position: relative;
        grid-row-start: 1;
        grid-column-start: 1;
        grid-row-end: 2;
        grid-column-end: 2;
        overflow: hidden;
        .picture__contentent__bob__dopsvoystvo{
         height: 100%;
         flex-direction: column;
         align-items: start;
       }
       .picture__contentent__top{
         height: 100%;
         h3{
            font-size: 40px;
            @media (max-width:1040px){
               font-size: 30px;
            }
         }
         p{
            font-size: 22px;
            @media (max-width:1040px){
               font-size: 20px;
            }
         }
       }
     }
     #item-1 {
      position: relative;
        grid-row-start: 1;
        grid-column-start: 2;
        grid-row-end: 2;
        grid-column-end: 3;
     }
     #item-2 {
      position: relative;
        grid-row-start: 1;
        grid-column-start: 3;
        grid-row-end: 3;
        grid-column-end: 4;
        overflow: hidden;
        
     }
     #item-3 {
      position: relative;
        grid-row-start: 2;
        grid-column-start: 1;
        grid-row-end: 4;
        grid-column-end: 3;
     }
     #item-4 {
      position: relative;
        grid-row-start: 3;
        grid-column-start: 3;
        grid-row-end: 4;
        grid-column-end: 4;
     }
}
.picture__container__cards_2,
.picture__container__cards_3{
    display: grid; 

    grid-template-rows: 0.1fr 0.8fr 0.1fr;
    grid-template-columns: 2fr 2fr 1fr;
    
    gap: 20px;
    height: 100%;
    @media (max-width:650px){
      display: block;
   }
    video{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    #item-0 {
      position: relative;
        grid-row-start: 1;
        grid-column-start: 2;
     
        grid-row-end: 2;
        grid-column-end: 3;
        
     }
     #item-1 {
      position: relative;
        grid-row-start: 1;
        grid-column-start: 3;
     
        grid-row-end: 2;
        grid-column-end: 4;
        
     }
     #item-2 {
      position: relative;
        grid-row-start: 1;
        grid-column-start: 1;
        grid-row-end: 3;
        grid-column-end: 2;
     }
     #item-3 {
      position: relative;
        grid-row-start: 3;
        grid-column-start: 1;
        grid-row-end: 4;
        grid-column-end: 2;
        
     }
     #item-4 {
      position: relative;
        grid-row-start: 2;
        grid-column-start: 2;
        grid-row-end: 4;
        grid-column-end: 4;
        overflow: hidden;

     }
}

.picture__title{
            font-size: 10vw;
            font-family: "BeatriceBold";
            color: #000;
            color: #2a4eea;
            text-align: center;
            text-transform: uppercase;
            @media (max-width:450px){
                font-size: 40px;
            }
}
.picture__images{
   height: 300px;
   margin-right: 20px;
   @media (max-width:650px){
      height: 200px;
   }
   img{
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
}
.picture__contentent{
   position: absolute;
   left: 0;
   top: 0;
   width: 100%;
   height: 0%;
   opacity: 0;
   backdrop-filter: blur(2px);
   background-color: #ffffff69;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   padding: 70px 30px 30px;
   transition: .2s;
   .picture__contentent__top{
      flex: 1;
      h3{
         font-size: 55px;
         text-transform: uppercase;
         font-family: "Gerhaus Regular";
         @media (max-width:1100px){
            font-size: 45px;
         }   
      }
      p{
         font-size: 27px;
         font-family: "HeroLight";
         @media (max-width:1100px){
            font-size: 25px;
         }
      }
   }
   .picture__contentent__bottom{
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      .picture__contentent__bottom__title{
         display: flex;
         align-items: center;
         gap: 10%;
         width: 100%;
         h3{
            font-size: 1.2vw;
            font-family: "Gerhaus Regular";
         }
         h4{
            font-size: 1.2vw;
            font-family: "HeroLight";
         }
      }
      .picture__contentent__bottom__img{
         width: 40%;
         img{
            width: 100%;
            object-fit: cover;
            &:not(:last-child){
               margin-bottom: 20px;
            }
         }
      }
      
   }
}

